.scrollable {
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;

    &.hover {

        &,
        &>.cell-inner {
            overflow-y: hidden !important;
        }

        &:hover,
        &:focus,
        &:active {
            overflow: visible;
            overflow-y: auto;

            &>.cell-inner {
                overflow-y: auto !important;
            }
        }
    }

    .smart & {

        &,
        &>.cell-inner {
            overflow-y: auto !important;
        }
    }
}

.scroll-x,
.scroll-y {
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
}

.scroll-y {
    overflow-y: auto;
}

.scroll-x {
    overflow-x: auto;
}

.hover-action {
    display: none;
}

.hover-rotate {
    transition: all 0.2s ease-in-out 0.1s;
}

.hover-anchor:hover,
.hover-anchor:focus,
.hover-anchor:active {
    &>.hover-action {
        display: inherit;
    }

    &>.hover-rotate {
        transform: rotate(90deg);
    }
}

.backdrop {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;

    &.fade {
        opacity: 0;
    }

    &.in {
        opacity: 0.8;
    }
}

