    height: 100%;

    &>.col {
        display: table-cell;
        vertical-align: top;
        height: 100%;
        float: none;
    }
}

.vbox {
    display: table;
    border-spacing: 0;
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 240px;

    & .row-row {
        display: table-row;
        height: 100%;

        & .cell {
            position: relative;
            height: 100%;
            width: 100%;

            .ie & {
                display: table-cell;
                overflow: auto;

                & .cell-inner {
                    overflow: visible !important;
                }
            }

            & .cell-inner {
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                -webkit-overflow-scrolling: touch;
                overflow: auto;
            }
        }
    }
}

.word-break {
    white-space: normal;
    word-break: break-all;
    word-wrap: break-word;
}

@media (min-width: 768px) {
    .col-sm-2-4 {
        width: 20%;
        float: left;
    }

    .col-sm-1-5 {
        width: 12.5%;
        float: left;
    }
}

@media (min-width: 992px) {
    .col-md-2-4 {
        width: 20%;
        float: left;
    }

    .col-md-1-5 {
        width: 12.5%;
        float: left;
    }
}

@media (min-width: 1200px) {
    .col-lg-2-4 {
        width: 20%;
        float: left;
    }

    .col-lg-1-5 {
        width: 12.5%;
        float: left;
    }
}

// sm
@media (min-width: 768px) and (max-width: 991px) {
    .hidden-sm.show {
        display: inherit !important;
    }

    .no-m-sm {
        margin: 0 !important;
    }

    .no-padder-sm {
        padding: 0 !important;
    }
}

/*phone*/
@media (max-width: 767px) {
    .w-auto-xs {
        width: auto;
    }

    .shift {
        display: none !important;
    }

    .shift.in {
        display: block !important;
    }

    .row-2 [class*="col"] {
        width: 50%;
        float: left;
    }

    .row-2 .col-0 {
        clear: none;
    }

    .row-2 li:nth-child(odd) {
        clear: left;
        margin-left: 0;
    }

    .text-center-xs {
        text-align: center;
    }

    .text-left-xs {
        text-align: left;
    }

    .text-right-xs {
        text-align: right;
    }

    .no-border-xs {
        border-width: 0;
    }

    .pull-none-xs {
        float: none !important;
    }

    .pull-right-xs {
        float: right !important;
    }

    .pull-left-xs {
        float: left !important;
    }

    .dropdown-menu.pull-none-xs {
        left: 0;
    }

    .hidden-xs.show {
        display: inherit !important;
    }

    .wrapper-lg,
    .wrapper-md {
        padding: 15px;
    }

    .padder-lg,
    .padder-md {
        padding-left: 15px;
        padding-right: 15px;
    }

    .no-m-xs {
        margin: 0 !important;
    }

    .no-padder-xs {
        padding: 0 !important;
    }
}